import { Component, OnInit, Input, forwardRef, Output, EventEmitter, SimpleChanges } from '@angular/core';
import { PxtFieldConfig } from '../../../../interfaces/pxt-field-config';
import { FormGroup, NG_VALUE_ACCESSOR, ControlValueAccessor } from '@angular/forms';
import { RequestBaseService } from '../../../../services/pxt-http/request-base.service';

const noop = () => {
};

export const CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR: any = {
  provide: NG_VALUE_ACCESSOR,
  useExisting: forwardRef(() => PxtSelectComponent),
  multi: true
};

@Component({
  selector: 'pxt-select',
  templateUrl: './pxt-select.component.html',
  styleUrls: ['./pxt-select.component.scss'],
  providers: [CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR]
})
export class PxtSelectComponent implements OnInit, ControlValueAccessor {

  @Input() required: boolean = false;
  @Input() placeholder: string = 'Selecione';
  @Input() model: any;
  @Input() params: any;
  @Output() selectionChange: EventEmitter<any> = new EventEmitter<any>();


  ngOnChanges(changes: SimpleChanges) {
    if (changes.params != undefined && !changes.params.firstChange) {
      this.params = changes.params.currentValue;
      this.find();
    }
  }

  controller = "";
  auto = false;

  private onTouchedCallback: () => void = noop;
  private onChangeCallback: (_: any) => void = noop;

  field: PxtFieldConfig;
  group: FormGroup;

  option: any;

  options: any[] = [];

  get selectedOption() {
    return this.option;
  }

  onBlur() {
    this.onTouchedCallback();
  }

  set selectedOption(f: any) {
    if (f != undefined) {
      if (f !== this.option) {
        this.option = f;
        this.onChangeCallback(f.codigo);
        this.selectionChange.emit(this.option);
      }
    }
  }

  writeValue(value: any) {
    this.option = value;
    this.onChangeCallback(value);
  }

  registerOnChange(fn: any) {
    this.onChangeCallback = fn;
  }

  registerOnTouched(fn: any) {
    this.onTouchedCallback = fn;
  }

  constructor(public pxthttp: RequestBaseService) {
  }

  ngOnInit() {
    if (this.model != undefined) {
      this.controller = this.model.constructor.name;
      this.field = this.model;
      this.auto = false;
    } else if (this.field != undefined && this.field.className != undefined) {
      this.controller = this.field.className.name;
      this.auto = true;
    }
  };

  find() {
    if (this.controller != undefined) {
      this.pxthttp.doGet(this.controller, this.params).subscribe(result => {
        if (this.auto) {
          this.field.options = result;
        } else {
          this.options = result;
        }
      });
    }
  };
  ngAfterViewInit() {
    this.find();
  };
}


