import { Component, OnInit, Input, forwardRef, Output, EventEmitter } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';

const noop = () => {
};

export const CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR: any = {
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => PxtDatepickerComponent),
    multi: true
};

@Component({
  selector: 'pxt-datepicker',
  templateUrl: './pxt-datepicker.component.html',
  styleUrls: ['./pxt-datepicker.component.scss'],
  providers: [CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR]
})
export class PxtDatepickerComponent implements OnInit, ControlValueAccessor {

  @Input() placeholder: String = "Escolha uma data";
  @Input() minDate: Date;
  @Input() maxDate: Date;
  @Input() inputDisabled: boolean = false;
  @Output() onChange: EventEmitter<boolean> = new EventEmitter();

  dateModel: Date;

  private onTouchedCallback: () => void = noop;
  private onChangeCallback: (_: any) => void = noop;

  get dataSelecionada() {
    return this.dateModel;
  }

  set dataSelecionada(d: Date) {
    if (d !== this.dateModel) {
      this.dateModel = d;
      this.onChangeCallback(d);
    }
  }

  onBlur() {
    this.onTouchedCallback();
  }

  writeValue(value: any) {
    this.dateModel = value;
    this.onChangeCallback(value);
  }

  registerOnChange(fn: any) {
    this.onChangeCallback = fn;
  }

  registerOnTouched(fn: any) {
      this.onTouchedCallback = fn;
  }

  constructor() {}

  ngOnInit() {
  }

  onDateChange() {
    this.onChange.emit(true);
  }

  clear() {
    this.writeValue(undefined);
    this.onDateChange();
  }
  
}