import { Component, ViewChild, ComponentFactoryResolver, Output, Input, EventEmitter } from '@angular/core';
import { PxtButton } from './model/pxt-submenus.model';
import { OptionsSubmenu } from './enum/option-submenu.enum';
import { PxtContentBody } from '../../directives/pxt-content-body';
import { PxtAppComponentService } from '../../services/pxt-app-components.service';
import { RequestBaseService } from '../../services/pxt-http/request-base.service';
import { Observable } from 'rxjs';
import { HttpHelperService } from '../../services/pxt-http/http-helper-service';
import { UpercaseFirst } from '../../pipes/uppercase-first';
import { ControllerPipe } from '../../pipes/controller.pipe';
import { ToastrService } from 'ngx-toastr';


@Component({
  selector: 'pxt-submenus',
  templateUrl: './pxt-submenus.component.html',
  styleUrls: ['./pxt-submenus.component.css']
})
export class PxtSubmenusComponent<T> {

  @Input() model?: T = {} as T;
  private urlService = "";

  @Output() listing: EventEmitter<T[]> = new EventEmitter();
  @Output() statusSave: EventEmitter<T[]> = new EventEmitter();
  @Output() statusDelete: EventEmitter<T[]> = new EventEmitter();
  @Input() controller?: String;

  save() {
    if (this.validationModel()) {
      this._serviceBase.save(this.model).subscribe(result => {
        this.statusSave.emit(result);
      });
    }
  };
  search() {
    this._serviceBase.load().subscribe(result => {
      this.listing.emit(result);
    });
  };
  delete(id) {
    this._serviceBase.delete(id).subscribe(result => {
      this.statusDelete.emit(result);
    });
  };
  clear() {
    this.model = {} as T;
  };
  add() {
    throw new Error("Method 'add()' not implemented.");
  };
  back() {
    throw new Error("Method 'back()' not implemented.");
  };
  cols = 3;
  colspan = 1;
  buttons: PxtButton[] = [];
  enableSave = true;
  enableBack = true;
  enableClear = true;
  enableSearch = true;
  enableAdd = true;
  enableDelete = true;

  constructor(public _pxtAppService: PxtAppComponentService,
    public _serviceBase: RequestBaseService,
    public helper: HttpHelperService,
    public notificationService: ToastrService) {
    this.buttons.push(new PxtButton("keyboard_backspace", "VOLTAR", true, OptionsSubmenu.VOLTAR));
    this.buttons.push(new PxtButton("add", "SALVAR", true, OptionsSubmenu.SALVAR));
    this.buttons.push(new PxtButton("add", "NOVO", true, OptionsSubmenu.NOVO));
    this.buttons.push(new PxtButton("delete", "LIMPAR", true, OptionsSubmenu.LIMPAR));
    this.buttons.push(new PxtButton("search", "PESQUISAR", true, OptionsSubmenu.PESQUISAR));
    this.buttons.push(new PxtButton("delete", "EXCLUIR", true, OptionsSubmenu.EXCLUIR));

    setTimeout(() => {
      debugger;
      this.urlService = helper.getApi() + new ControllerPipe().transform(this.model.constructor.name);
      this._serviceBase.urlServiceAuto = this.urlService;
    }, 100);
  }

  validationModel() {
    if (Object.keys(this.model).length > 0) {
      Object.keys(this.model).forEach(key => {
        if (this.model[key] != undefined && this.model[key] != "") {
          this.notificationService.error("Campo ObrigatÃ³rio", key.toString().toLocaleUpperCase());
          return false;
        }
      });
      return true;
    } else {
      debugger;
      this.notificationService.warning("Nenhum campo preenchido.", "Aviso!");
      return false;
    }
  };
}
