import { ComponentFactoryResolver, ComponentRef, Directive, Input, OnInit, ViewContainerRef } from "@angular/core";
import { FormGroup } from "@angular/forms";
import { PxtInputComponent } from "../modules/shared/components/pxt-input/pxt-input.component";
import { PxtButtonComponent } from "../modules/shared/components/pxt-button/pxt-button.component";
import { FieldConfig } from "../interfaces/field.interface";
import { PxtDateComponent } from "../modules/shared/components/pxt-date/pxt-date.component";
import { PxtCheckboxComponent } from "../modules/shared/components/pxt-checkbox/pxt-checkbox.component";
import { PxtRadiobuttonComponent } from "../modules/shared/components/pxt-radiobutton/pxt-radiobutton.component";
import { PxtSelectComponent } from "../modules/shared/components/pxt-select/pxt-select.component";
import { PxtInputFilterComponent } from "../modules/shared/components/pxt-input-filter/pxt-input-filter.component";

const componentMapper = {
  input: PxtInputComponent,
  button: PxtButtonComponent,
  date: PxtDateComponent,
  select: PxtSelectComponent,
  radiobutton: PxtRadiobuttonComponent,
  checkbox: PxtCheckboxComponent,
  filter: PxtInputFilterComponent,
};
@Directive({
  selector: "[dynamicFieldDialog]"
})
export class DynamicFieldDirectiveDialog implements OnInit {
  @Input() field: FieldConfig;
  @Input() group: FormGroup;
  componentRef: any;
  constructor(
    private resolver: ComponentFactoryResolver,
    private container: ViewContainerRef
  ) { }
  ngOnInit() {
    const factory = this.resolver.resolveComponentFactory(
      componentMapper[this.field.type]
    );
    this.componentRef = this.container.createComponent(factory);
    this.componentRef.instance.field = this.field;
    this.componentRef.instance.group = this.group;
  }
}
