import { Component, OnInit, Inject, Input, ViewChild } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA, MatTableDataSource, MatPaginator, MatSort } from '@angular/material';
import { RequestBaseService } from '../../../services/pxt-http/request-base.service';


@Component({
  selector: 'pxt-dialog-filter-custom',
  templateUrl: './pxt-dialog-filter-custom.component.html',
  styleUrls: ['./pxt-dialog-filter-custom.component.scss']
})
export class PxtDialogFilterCustomComponent implements OnInit {

  @Input() filters: any;
  @Input() model: any;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  
  displayedColumns = ['codigo', 'descricao'];
  dataSource = new MatTableDataSource<any>();

  constructor(private fb: FormBuilder,
    private dialogRef: MatDialogRef<PxtDialogFilterCustomComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public http: RequestBaseService) { }

  ngOnInit() {
  }
  //Search.
  search() {
    let params = new Map<any, any>();
    if (this.filters !== undefined) {
      Object.keys(this.filters).forEach(key => {
        if (this.filters[key] != undefined) {
          params.set(key, this.filters[key]);
        }
      });
    }
    this.http.doGet(this.model.constructor.name, params).subscribe(result => {
      this.dataSource.data = result;
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    });
  };

  //Row Selected
  selectRow(row) {
    this.dialogRef.close(row);
  };

  //Close
  close() {
    this.dialogRef.close(undefined);
  };

  ngAfterViewInit() {
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }
}
